.todoWrapper {
    position: relative;
    min-height: 55px;
    display: flex;
    border-bottom: 1px solid #fefefe;
}

.todoItem {
    width: 100%;
    padding: 1rem;
    /* background-color: rgb(107, 107, 107); */
    display: flex;
    align-items: center;
    gap: 10px;
    z-index: 2;
    transition: transform 0.5s ease-out;

}

.todoItem.open {
    transform: translateX(-80px);
}

.todoItem span {
    flex: 1;
}



.actionBtn {
    /* ... other styles ... */
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.actionBtn span {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}