.inputContainer {
    position: relative;
    width: 100%;
    height: 70px;
    margin-top: auto;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;

}

.inputForm {
    width: 30px;
    height: 30px;
    background-color: #fff;
    box-shadow: 0px 5px 50px rgba(83, 83, 83, 0.5);
    border-radius: 10px;
    overflow: hidden;
    opacity: 0;
    position: absolute;
    top: 50px;
    right: 0;
    transform: translate(50%, -50%);

}

.inputForm.open {
    width: 500px;
    height: 70px;
    right: 50%;
    border-radius: 10px;
    opacity: 1;
}

.inputForm input {
    width: 100%;
    height: 100%;
    padding: 0 1rem;
    font-size: 1rem;
    border: none;
    /* outline: auto; */
}

.addButton {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 35px;
    width: 35px;
    background-color: grey;
    border: none;
    outline: none;
    border-radius: 10px;
    /* Adjust the value to change the roundness of the corners */
    box-shadow: 0px 5px 50px rgba(83, 83, 83, 0.5);
    cursor: pointer;
    color: #ffffff;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}


.addButton:hover {
    scale: 1.05;
}

.addButton:active {
    scale: 1.05;
}