.todoList{
    width: 500px;
    max-height: 80%;
    /* height: 500px; */
    background: rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    z-index: 1;
    margin-top: 3rem;
    margin-bottom: 1rem;
    box-shadow: var(--shadow1);
    overflow-y: auto;

    box-shadow: 0px 5px 50px rgba(83, 83, 83, 0.5);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px); /* For Safari */
}